@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900);
:root{--primary: #3f51b5;--light: lightgray}*,*::before,*::after{padding:0;margin:0;box-sizing:border-box}body{font-family:"Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.w-100{width:100%}.text-transform-none{text-transform:none !important}.cursor-pointer{cursor:pointer}.text-right{text-align:right !important}.py-0{padding-top:0px !important;padding-bottom:0px !important}.px-0{padding-left:0px !important;padding-right:0px !important}.pt-0{padding-top:0px !important}.pr-0{padding-right:0px !important}.pb-0{padding-bottom:0px !important}.pl-0{padding-left:0px !important}.d-block{display:block !important}.d-flex{display:flex !important}.align-items-center{align-items:center !important}.place-content-end{place-content:flex-end}.gap-5{grid-gap:5px;gap:5px}.gap-10{grid-gap:10px !important;gap:10px !important}.flax-no-wrap{flex-wrap:nowrap !important}.mb-3{margin-bottom:1.5rem !important}.mt-2{margin-top:1rem !important}.ml-2{margin-left:1rem !important}.mr-1{margin-right:0.768rem !important}

.task{margin-bottom:15px;border:1px solid lightgray;padding:5px 15px;cursor:pointer;position:relative}.task .list-item-width{min-width:35px !important}.task .label{display:flex;flex-wrap:wrap;grid-gap:5px;gap:5px}.task .text-with-icon,.task .text-with-icon>svg{display:flex;align-items:center;grid-gap:5px;gap:5px;font-size:15px;color:gray}.task .notes{font-size:15px;color:gray}.task .task-more-options{position:absolute;right:0px;top:2px}


.custom-modal{max-height:500px;position:absolute;top:10%;left:25%;overflow-y:scroll}.custom-modal .modal-body{padding:30px;width:700px;background-color:#fff;border-radius:5px}.custom-modal .checklist-progress{position:relative;top:20px}.custom-modal .list-item-width{min-width:35px !important}.custom-modal .modal-footer{display:flex;justify-content:flex-end}.custom-modal .select-control>div{display:flex;align-items:center;grid-gap:10px;gap:10px}

.tasks-main .main-container{display:flex;flex-wrap:nowrap;padding:20px;grid-gap:30px;gap:30px}.tasks-main .bucket{display:flex;flex-direction:column;min-width:300px;max-width:300px}.tasks-main .bucket-top{display:flex;justify-content:space-between;align-items:center;height:30px}.tasks-main .btn{padding:5px;font-size:0.9rem;text-transform:none;font-weight:700;color:var(--primary);padding:0px 10px;letter-spacing:0px}.tasks-main .bucket-options-list{width:200px}.tasks-main .add-new-task-block{margin-top:30px}.tasks-main .add-new-task{margin-top:10px;margin-bottom:15px;display:flex;flex-direction:column;grid-gap:15px;gap:15px;padding:15px;border:1px solid lightgray;width:300px}.tasks-main .list-item-width{min-width:35px !important}.tasks-main .according-header-bg-color{background-color:var(--light)}.tasks-main .pending-tasks{min-height:50px}


